"use client";

import { useState, useEffect } from "react";
import { Icons } from "@/components/ui/icons";
import { AgentCard } from "@/app/dashboard/agents/_components/AgentCard";
import { AgentsHeader } from "@/app/dashboard/agents/_components/AgentsHeader";
import { AgentsEmptyState } from "@/app/dashboard/agents/_components/AgentsEmptyState";
import CharacterManagementService from "../../../../service/CharacterManagementService";
import { toast } from "sonner";

interface Agent {
  name: string;
  status: "running" | "stopped";
  schedule: {
    POST_INTERVAL_MINUTES: number;
    content_focus: string[];
    trend_focus: string[];
  };
  profile: {
    description: string;
    personality: string[];
    expertise: string[];
  };
}

export default function AgentsPage() {
  const [agents, setAgents] = useState<Agent[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const service = new CharacterManagementService();

  useEffect(() => {
    loadAgents();
  }, []);

  const loadAgents = async () => {
    setIsLoading(true);
    try {
      const savedCoin = localStorage.getItem("selectedMemeCoin");
      if (savedCoin) {
        const coin = JSON.parse(savedCoin);
        const [schedule, profile, status] = await Promise.all([
          service.getCharacterSchedule(coin.name),
          service.getCharacterProfile(coin.name),
          service.getCharacterStatus(coin.name),
        ]);

        setAgents([
          {
            name: coin.name,
            status: status.isRunning ? "running" : "stopped",
            schedule: schedule.twitter,
            profile: profile,
          },
        ]);
      }
    } catch (error) {
      console.error("Failed to load agents:", error);
      toast.error("Failed to load agents");
    } finally {
      setIsLoading(false);
    }
  };

  const handleStatusToggle = async (
    agentName: string,
    currentStatus: "running" | "stopped"
  ) => {
    try {
      if (currentStatus === "running") {
        await service.stopCharacter(agentName);
      } else {
        await service.startCharacter(agentName);
      }
      await loadAgents();
      toast.success(
        `Agent ${
          currentStatus === "running" ? "stopped" : "started"
        } successfully`
      );
    } catch (error) {
      console.error("Failed to toggle agent status:", error);
      toast.error("Failed to update agent status");
    }
  };

  if (isLoading) {
    return (
      <div className="container max-w-6xl mx-auto px-4 py-16 text-center">
        <Icons.spinner className="w-8 h-8 animate-spin mx-auto" />
      </div>
    );
  }

  return (
    <div className="container max-w-6xl mx-auto px-4 py-16">
      <AgentsHeader />

      {agents.length === 0 ? (
        <AgentsEmptyState />
      ) : (
        <div className="grid gap-6">
          {agents.map((agent) => (
            <AgentCard
              key={agent.name}
              name={agent.name}
              status={agent.status}
              description={agent.profile.description}
              expertise={agent.profile.expertise}
              schedule={agent.schedule}
              onStatusToggle={() =>
                handleStatusToggle(agent.name, agent.status)
              }
            />
          ))}
        </div>
      )}
    </div>
  );
}
